import React, {useState, useEffect, useRef} from 'react';
import SideBar from './SideBar'
import Kakaomap from './Kakaomap'
import background from '../img/main.jpg'
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import BackGround from "./BackGround";
import logo from '../img/teamlogo.png'
import styles from '../CSS/Main.css'
import Button from "react-bootstrap/Button";
import Footer from "./Footer";

let isSidebar = false;

export default function Main(props) {
    const [isLogin , setIsLogin] = useState(null);
    const loginHandler = () => {
        axios.get('/member/getLoginInfo').then( res => { setIsLogin(res.data); alert(res.data);})
    };
    let sidebar = useRef(null);
    console.log(sidebar);
    const sidebarHandler = ( value ) =>{

        sidebar.style.left = value ;
        isSidebar = !isSidebar;
    }

    useEffect(loginHandler,[]);

    return(
        <>
            <Footer />
            {
                isLogin==''?
                    (
                    <>
                        <BackGround />
                    </>
                    )
                    :
                    (
                        <div>
                            <div className={"mainbox"}>
                                <div>
                                    {
                                        isSidebar != false ?
                                            (<button onClick={ ()=>{ sidebarHandler(0); } } className={"sidebtn"} id={"sidebtn"} > 인기 게시물 </button>)
                                        :
                                            null
                                    }
                                </div>
                                <div className="sidebar"> <SideBar ref={sidebar} onHide={sidebarHandler} position={isSidebar} className={"sidebar"}/></div>
                                <div className="mapbox">  <Kakaomap/>  </div>
                            </div>
                        </div>
                    )
            }
        </>
    );
}